<template>
	<div class="collectMatter">
		<div class="cmTitles">
			<p>当前位置：工作台 >采集素材</p>
		</div>
		<div class="cmHeader">
			<el-tabs type="border-card">
				<el-tab-pane>
					<span slot="label"><i class="iconfont icon-jichengguanxi"></i>互联网</span>
					<collectSearch></collectSearch>
				</el-tab-pane>
				<el-tab-pane>
					<span slot="label"><i class="iconfont icon-jichengguanxi"></i>国家</span>
					<collectSearch></collectSearch>
				</el-tab-pane>
				<el-tab-pane>
					<span slot="label"><i class="iconfont icon-jichengguanxi"></i>北京</span>
					<collectSearch></collectSearch>
				</el-tab-pane>
				<el-tab-pane>
					<span slot="label"><i class="iconfont icon-jichengguanxi"></i>东盟</span>
					<collectSearch></collectSearch>
				</el-tab-pane>
				<el-tab-pane>
					<span slot="label"><i class="iconfont icon-jichengguanxi"></i>XXX</span>
					<collectSearch></collectSearch>
				</el-tab-pane>
				<el-tab-pane>
					<span slot="label"><i class="iconfont icon-jichengguanxi"></i>XXXX</span>
					<collectSearch></collectSearch>
				</el-tab-pane>
				<el-tab-pane>
					<span slot="label"><i class="iconfont icon-jichengguanxi"></i>XXXX</span>
					<collectSearch></collectSearch>
				</el-tab-pane>
			</el-tabs>
		</div>
		<div class="cmContent">
			<p class="title">共搜索12000条信息</p>
			<ul id="content">
				<li class="con" v-for="(val,key) in list">
					<dl>
						<dt v-html="val.title"></dt>
						<dd><i class="iconfont icon-fangzi-copy"></i>来源：<span v-html="val.resource"></span></dd>
						<dd><i class="iconfont icon-shijian"></i>时间：<span v-html="val.time"></span></dd>
					</dl>
					<p class="content" v-html="val.con"></p>
					<p class="details">
						<router-link :to="{path:'/xiangqing/'+val.id}">http://www.baidu.com</router-link>
					</p>
				</li>
			</ul>
			<div class="block">
				<el-pagination layout="prev, pager, next" :total="50">
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import collectSearch from "@/pages/cmSearch"
	export default {
		data() {
			return {
				list: {
					type: Array
				},
				flag: false,
				xz: "",
				act: true
			}
		},
		components: {
			collectSearch
		},
		mounted() {
			this.axios.get('http://localhost:8088/static/db.json').then((res) => {
				//console.log(res.data.collectContent)
				this.list = res.data.collectContent;
				console.log(this.list)
			})
		},
		methods: {
			show() {
				this.flag = !this.flag
			}
		}
	}
</script>

<style lang="less">
	.collectMatter {
		width: 1024px;
		margin: 0 auto;
		min-width: 1024px;
		.cmTitles {
			width: 984px;
			margin: 0 20px;
			p {
				height: 46px;
				line-height: 46px;
			}
		}
		.cmHeader {
			width: 984px;
			margin: 0 20px 0 20px;
			height: 202px;
			.el-tabs {
				.el-tabs__header {
					.el-tabs__nav-wrap {
						.el-tabs__nav-scroll {
							.el-tabs__nav {
								background: #eef1f7;
								.el-tabs__item {
									width: 142px;
									text-align: center;
									height: 42px;
									line-height: 42px;
									border-bottom: 1px solid #cfd0d0;
									border-right: 1px solid #cfd0d0;
									border-top: 2px solid transparent;
									transition: all .2s;
									color: #787878;
								}
								.el-tabs__item:last-child {
									border-right: none;
								}
								.is-active {
									border-top: 2px solid red;
									border-bottom: none;
									color: #787878;
								}
							}
						}
					}
				}
			}
			#area {
				display: flex;
				width: 984px;
				overflow: hidden;
				li {
					float: left;
					width: 142px;
					height: 42px;
					line-height: 42px;
					text-align: center;
					background: #eef1f7;
					color: #787878;
					border-bottom: 1px solid #cfd0d0;
					border-left: 1px solid #cfd0d0;
					cursor: pointer;
				}
				li:first-child {
					border-left: none;
				}
				.act {
					border-top: 2px solid red;
					background: #fff;
					border-bottom: none;
				}
			}
		}
		.cmContent {
			width: 984px;
			padding: 0 35px;
			margin: 10px 20px 23px 20px;
			background: #fff;
			padding-bottom: 56px;
			box-shadow: 0 0px 4px 0 rgba(0,0,0,.12), 0 0 10px 0 rgba(0,0,0,.04);
			.title {
				padding: 30px 0;
			}
			#content {
				.con {
					margin-bottom: 30px;
					dl {
						margin: 4px 0;
						height: 21px;
						overflow: hidden;
						dt {
							float: left;
							display: inline-block;
							.hight {
								color: red;
							}
						}
						dd {
							float: left;
							margin-left: 44px;
							display: inline-block;
							color: #777777;
							i {
								margin-right: 8px;
							}
						}
						dd:last-child {
							margin-left: 24px;
						}
					}
					.content {
						line-height: 26px;
						font-size: 15px;
						color: #666666;
						overflow: hidden;
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
					}
					.details {
						line-height: 30px;
						a {
							color: #00806f;
						}
					}
				}
			}
			.block {
				margin: 0 auto;
				.el-pagination {
					text-align: center;
					.el-pager{
						.number{
							margin: 0 4px;
							border: 1px solid #d1dbe5;
							border-radius: 2px;
							width: 26px;
							height: 26px;
							text-align: center;
							line-height: 26px;
						}
						.active{
							border: none;
						}
					}
					button{
						border: 1px solid #d1dbe5;
						margin: 0 4px;
						border-radius: 2px;
						width: 26px;
						height: 26px;
					}
				}
			}
		}
	}
</style>


