import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/login'
import cmSearch from '@/pages/cmSearch'

Vue.use(Router)

export default new Router({
	hashbang: false,
	history: true,
	mode: 'history',
    routes: [{
        path: '/',
        component: login
    }]
})


