<template>
	<div class="search">
		<div class="sTitle">
			<p>查询条件</p>
		</div>
		<div class="item">
			<div class="itemCon">
				<div class="itemLable">
					搜索：
				</div>
				<div class="ipt">
					<div id="ss">
						<el-input v-model="input" placeholder="请输入内容"></el-input>
					</div>
				</div>
			</div>
			<div class="itemCon">
				<div class="itemLable">
					时间：
				</div>
				<div class="ipt">
					<el-date-picker id="bt" v-model="value1" type="date" placeholder="选择日期" :picker-options="pickerOptions0">
					</el-date-picker>&nbsp;&nbsp;--&nbsp;
					<el-date-picker id="bt" v-model="value2" type="date" placeholder="选择日期" :picker-options="pickerOptions0">
					</el-date-picker>
				</div>
			</div>
			<div class="itemCon">
				<div class="itemLable">
					来源：
				</div>
				<div class="ipt">
					<div class="select">
						<el-select v-model="value3" placeholder="请选择...">
							<el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled">
							</el-option>
						</el-select>
					</div>
					<!--<i class="iconfont icon-youjian1" @click="show"></i>
							<p v-model="xz" @click="show">请选择...</p>
							<ul v-if="flag">
								<li v-model="bxz">请选择...</li>
								<li v-model="bxz">哈哈哈哈</li>
							</ul>-->
				</div>
			</div>
			<div class="itemCon">
				<button id="refer">查询</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				input:'',
				pickerOptions0: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},
				pickerOptions1: {
					shortcuts: [{
						text: '今天',
						onClick(picker) {
							picker.$emit('pick', new Date());
						}
					}, {
						text: '昨天',
						onClick(picker) {
							const date = new Date();
							date.setTime(date.getTime() - 3600 * 1000 * 24);
							picker.$emit('pick', date);
						}
					}, {
						text: '一周前',
						onClick(picker) {
							const date = new Date();
							date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', date);
						}
					}]
				},
				value1: '',
				value2: '',
				options2: [{
					value: '选项1',
					label: '黄金糕'
				}, {
					value: '选项2',
					label: '双皮奶',
					disabled: true
				}, {
					value: '选项3',
					label: '蚵仔煎'
				}, {
					value: '选项4',
					label: '龙须面'
				}, {
					value: '选项5',
					label: '北京烤鸭'
				}],
				value3: ''
			}
		}
	}
</script>

<style lang="less">
	.search {
		width: 944px;
		padding: 0 20px;
		height: 78px;
		.sTitle {
			width: 904px;
			border-bottom: 1px solid #e6e6e6;
			p {
				height: 48px;
				line-height: 49px;
				color: #333333;
			}
		}
		.item {
			position: relative;
			.itemCon {
				padding: 26px 0;
				float: left;
				margin-right: 24px;
				.itemLable {
					width: 76px;
					height: 28px;
					line-height: 28px;
					text-align: center;
					color: #333;
					float: left;
					font-size: 14px;
				}
				.ipt {
					float: left;
					position: relative;			
					.select {
						width: 150px;
						height: 26px;
						margin:0;
					}
				}
				#refer {
					width: 60px;
					height: 34px;
					border-radius: 4px;
					background: #f33535;
					color: #ffffff;
					border: none;
					position: absolute;
					right: 14px;
					top: 22px;
				}
			}
			#ss {
				width: 165px;
				height: 26px;
				border-radius: 4px;
			}
			#bt,
			#at,
			{
				width: 90px;
				height: 26px;
				padding: 0;
				text-align: center;
				i{
					display: none;
				}
			}
			.el-input__inner {
				width: 100%;
				height: 26px;
				padding: 3px 0 3px 10px;
			}
		}
	}
</style>


